import { useEffect, useState } from 'react';
import Select from 'react-select';

function Countries({ countries, updateActiveCountry }) {
    const [displayItems, updateDisplayItems] = useState([]);
    useEffect(() => {
        const displayData = [];
        countries?.forEach((item) => displayData.push({ ...item, value: item.country, label: item.country }))
        updateDisplayItems(displayData);
    }, [countries])
    return (
        <div className="countriesWrapper">
            <h3>Countries</h3>
           {/* countries.map(( {country} , i) => 
           <div key={i}  className="country" onClick={() => updateActiveCountry(country)}>{country}</div>) */}
           <Select
                    options={displayItems}
                    value={displayItems.value}
                    onChange={data => {  updateActiveCountry(data.value)}}
                    defaultValue={displayItems.value} />
        
        </div>
    )
    
}

export default Countries;