import { useState, useEffect } from 'react';
import './App.css';
import Countries from './components/Countries';
import States from './components/States';
import { useAppContext } from './AppContext';


function Country() {
  const [countries, updateCountries] = useState([]);
  const [activeCountry, updateActiveCountry] = useState('');
  const [states, updateStates] = useState([]);
  // const [activeStates, updateActiveStates] = useState([]);
  
  const { activeStates, updateActiveStates, activeDisticts, updateDistricts } = useAppContext();

  useEffect(() => {
    async function getData(){
      const Countries_URL='https://countriesnow.space/api/v0.1/countries';
      const Countries_Res= fetch(Countries_URL);
      const Countries_Response= await Countries_Res;
      const Countries_json= await Countries_Response.json();
      console.log('countries', Countries_json.data);
      updateCountries(Countries_json.data);
      const States_URL='https://countriesnow.space/api/v0.1/countries/states';
      const States_Res= fetch(States_URL);
      const States_Response= await States_Res;
      const States_json= await States_Response.json();
      console.log('states', States_json.data);
      updateStates(States_json.data);
      const districts_URL='https://data.covid19india.org/state_district_wise.json';
      const districts_Res= fetch(districts_URL);
      const districts_Response= await districts_Res;
      const districts_json = await districts_Response.json();
      updateDistricts(districts_json);
      console.log('districts_json', districts_json);
    }
    getData();
  }, []);

  useEffect(() => {
    if (activeCountry !== '') {
      let data = states.filter(({ name }) => name === activeCountry );
      updateActiveStates(data && data[0]);
    }
  }, [activeCountry]);
  return (
    <div className="Country">
      <div className="countriesStateWrapper">
        <Countries countries={countries} updateActiveCountry={updateActiveCountry} />
        <States activeStates={activeStates} />
      </div>
    </div>
    
  );
}

export default Country;
