import React, { useState,useEffect } from "react";
import { sha256 } from 'js-sha256';

const Download = () => {
    const [mobile, updateMobile] = useState('');
    const [otp, updateOtp] = useState('');
    const [tokenDetails, updateToken] = useState('');
    const [downloadToken, updateDownloadToken] = useState('');
    const handleOtp = async () => {
        fetch('https://cdn-api.co-vin.in/api/v2/auth/public/generateOTP', {
            method: 'post',
            headers: {'Content-Type':'application/json'},
            body: JSON.stringify({
             "mobile": mobile
            })
           }).then((response) => response.json())
           .then((responseJson) => {
               console.log(responseJson);
            updateToken(responseJson.txnId);
           })
           .catch((error) => {
             console.error(error);
           });
    }
    console.log('tokenDetails', tokenDetails);
    const handleLogin = async () => {
      let otpPayload = sha256(otp);
      fetch('https://cdn-api.co-vin.in/api/v2/auth/public/confirmOTP', {
        method: 'post',
        headers: {'Content-Type':'application/json'},
        body: JSON.stringify({
         "otp": otpPayload,
         "txnId": tokenDetails
        })
       }).then((response) => response.json())
       .then((responseJson) => {
         console.log('validate', responseJson);
         updateDownloadToken(responseJson.token);
       })
       .catch((error) => {
         console.error(error);
       });
    }
    console.log('downloadToken',downloadToken);
    const handleDownload = () => {
      fetch('https://cdn-api.co-vin.in/api/v2/registration/public/download?beneficiary_reference_id=32581445648440', {
            method: 'post',
            headers: {'Content-Type':'application/json', 
            'Authorization': downloadToken},
            // body: JSON.stringify//({
            //  "mobile": mobile
            // })
           }).then((response) => response.json())
           .then((responseJson) => {
               console.log(responseJson);
            updateToken(responseJson.txnId);
           })
           .catch((error) => {
             console.error(error);
           });
    }
    return (
        <><div>Login</div>
        <div>Mobile <input type="text" value={mobile} onChange={(e) => updateMobile(e.target.value)}/></div>
        <button onClick={handleOtp}>Send otp</button>
        <div>Otp <input type="text" value={otp} onChange={(e) => updateOtp(e.target.value)}/></div>
        <button onClick={handleLogin}>Login</button>
        <button onClick={handleDownload}>download</button>
        </>
    );
}
export default Download;