import React, { useEffect, useState} from "react";
import Select from 'react-select';
import { useAppContext } from './AppContext';

const Covid19 = () => {
    const { selectedState, updateSelectedState, activeStates: { name, states } = {}, activeDisticts } = useAppContext();
    const [activeDist, updateDist] = useState({});
    const [displayItems, updateDisplayItems] = useState([]);
    console.log('activeDist', activeDist)
    console.log('displayItems', displayItems)
    useEffect(() => {
        const data = activeDisticts[selectedState.name];
        updateDist(data?.districtData);
        const displayData = [];
        states?.forEach((item) => displayData.push({ ...item, value: item.name, label: item.name }))
        updateDisplayItems(displayData);
      }, [selectedState.name]);

    return (
    <><h1>Covid Affected districts</h1><div className="c-19-wrapper">
            <div className="left-section">
            <h3>{name} - States</h3>
                <Select
                    options={displayItems}
                    value={displayItems.value}
                    onChange={value => updateSelectedState(value)}
                    defaultValue={displayItems.value} />
            </div>
            <div className="right-section">
            <h3>Districts</h3>
                {activeDist && 
                  Object.keys(activeDist).map((value, index) => {
                      let item = activeDist[value];
                      return  (
                      <div key={index} className="dist-wrap">
                          <h4>{value}</h4>
                          <div>Active: {item.active}</div>
                          <div>Confirmed: {item.confirmed}</div>
                          <div>Recovered: {item.recovered}</div>
                          <div>Deceased: {item.deceased}</div>
                      </div>
                  )})
                  
                }
            </div>
        </div></>
    )
}
export default Covid19;