import { useAppContext } from '../AppContext';
import { useNavigate } from 'react-router-dom';
function States({ activeStates: { name, states } = {} }) {
    const { updateSelectedState } = useAppContext();
    const history = useNavigate ();
    const handleClick = (state) => {
        updateSelectedState(state);
        history('/covid19');
    }
    return (
        <div className="statesWrapper">
            <h3>{name} - States</h3>
            {states?.map((state, index) => <div key={index} className="state" onClick={() => handleClick(state)}>{index + 1}. {state.name}</div>)}
        </div>
    )
}

export default States;