import React, { createContext, useState } from "react";
import Country from './Country';
import Covid19 from './Covid19';
import Download from './Download';
// import "./components/Navbar/Navbar.css";
import {BrowserRouter as Router, Route, Link,  Routes} from "react-router-dom";
import { AppContext } from "./AppContext";
import "./App.css";



const Layout = () => {
  return (
    <>
      <nav className="Layout">
        <ul className="nav-links">
          <li>
            <Link to="/country" className="nav-links1">Countries</Link>
          </li>
          <li>
            <Link to="/covid19" className="nav-links1">Covid19</Link>
          </li>
          <li>
            <Link to="/download" className="nav-links1">Download Vaccination Certificate</Link>
          </li>
        </ul>
      </nav>
    </>
  )
};



function App(){
    const [activeStates, updateActiveStates] = useState([]);
    const [selectedState, updateSelectedState] = useState({});
    const [activeDisticts, updateDistricts] = useState({});
    console.log('activeStates', activeStates);
    console.log('selectedState', selectedState);
    return(
        <div className="App1">
          <AppContext.Provider value={{activeStates, updateActiveStates, 
            selectedState, updateSelectedState, activeDisticts, updateDistricts}}>
        <Router>
        <Layout />
            <Routes>
                <Route path="/country" element={<Country />}/>
                <Route path="/covid19" element={<Covid19 />}/>
                <Route path="/download" element={<Download />}/>
            </Routes>
        </Router>
        </AppContext.Provider>
        </div>
    )
}
export default App;